}

#tat {
  margin-left: 10%;
}

#gal {
  margin-left: 10%;
}

#maya {
  position: fixed;
  bottom: 0;
  right: 0;
}

.tag {
  text-align: center;
  min-width: 100%;
  font-weight: bold;
}

.center {
  color: red;
  text-align: center;
  min-width: 100%;
}

.nameWrapper {
  height: 50px;
  background-color: #FFFFFF!;
}

video {
  margin: 0 auto;
  /* border: 2px dotted  white; */
  border-radius: 20px;
}

.name {
  font-size: calc(60px + 2vmin);
  font-weight: bold;
  font-family: 'Caveat', cursive;
  color: black;
  text-shadow:  -6px -2px red, 6px 2px #FFFFFF, 0 0 60px indianred;
  margin-left: 2%;
  min-width: 100%;
  position: fixed;
  top: 0;
  line-height: 0;
  font-family: 'Roboto Slab', serif;
}

